// This file serves as the entry point for the Cloudflare Worker

export default {
  async fetch(request, env, ctx) {
    const url = new URL(request.url);
    
    // Handle API requests 
    if (url.pathname.startsWith('/api/')) {
      // API endpoint to get all data
      if (url.pathname === '/api/data' && request.method === 'GET') {
        try {
          // Get data from KV store
          const stores = await env.STORE_DATA.get('stores', { type: 'json' });
          const footerLinks = await env.STORE_DATA.get('footerLinks', { type: 'json' });
          const admins = await env.STORE_DATA.get('admins', { type: 'json' });
          
          return new Response(JSON.stringify({ 
            stores: stores || {}, 
            footerLinks: footerLinks || {},
            admins: admins || []
          }), {
            headers: {
              'Content-Type': 'application/json',
            },
          });
        } catch (error) {
          return new Response(
            JSON.stringify({ 
              success: false, 
              error: error.message 
            }), {
              status: 500,
              headers: {
                'Content-Type': 'application/json',
              },
            }
          );
        }
      }
      
      // API endpoint to save data
      if (url.pathname === '/api/admin/save' && request.method === 'POST') {
        try {
          // Parse the request body
          const data = await request.json();
          
          // Save stores data
          if (data.stores) {
            await env.STORE_DATA.put('stores', JSON.stringify(data.stores));
          }
          
          // Save footer links data
          if (data.footerLinks) {
            await env.STORE_DATA.put('footerLinks', JSON.stringify(data.footerLinks));
          }
          
          return new Response(JSON.stringify({ success: true }), {
            headers: {
              'Content-Type': 'application/json',
            },
          });
        } catch (error) {
          return new Response(
            JSON.stringify({ 
              success: false, 
              error: error.message 
            }), {
              status: 500,
              headers: {
                'Content-Type': 'application/json',
              },
            }
          );
        }
      }
      
      // API endpoint to initialize KV from db.json
      if (url.pathname === '/api/admin/init-kv' && request.method === 'POST') {
        try {
          // This endpoint is handled by the init-kv script
          // Just return a success message
          return new Response(JSON.stringify({ 
            success: true,
            message: 'Use the npm run init-kv command to initialize the KV store'
          }), {
            headers: {
              'Content-Type': 'application/json',
            },
          });
        } catch (error) {
          return new Response(
            JSON.stringify({ 
              success: false, 
              error: error.message 
            }), {
              status: 500,
              headers: {
                'Content-Type': 'application/json',
              },
            }
          );
        }
      }
      
      // If we get here, it means the API endpoint was not found
      return new Response(JSON.stringify({ 
        success: false, 
        error: 'API endpoint not found' 
      }), {
        status: 404,
        headers: {
          'Content-Type': 'application/json',
        },
      });
    }
    
    // For non-API requests, check if ASSETS binding exists (in Pages environment)
    if (env.ASSETS) {
      return await env.ASSETS.fetch(request);
    }
    
    // For non-API requests in pure Workers environment with no ASSETS binding
    // Return a basic response for the root path
    if (url.pathname === '/' || url.pathname === '') {
      return new Response('Welcome to stores.deals API. Web app is served from Cloudflare Pages.', {
        headers: {
          'Content-Type': 'text/plain',
        },
      });
    }
    
    // Any other paths not handled return 404
    return new Response('Not found', {
      status: 404,
      headers: {
        'Content-Type': 'text/plain',
      },
    });
  },
}; 